@font-face {
  font-family: Avenir;
  src: url("Avenir-Book.657c3568.ttf"), url("Avenir-Heavy.01d9ab74.ttf"), url("Avenir.8bcf8c72.ttc");
}

body {
  height: 100vh;
}

.big, .small {
  margin-top: 20px;
}

.big__section {
  height: 70px;
}

.small__section {
  height: 42px;
}

.big__section, .small__section {
  align-items: center;
  display: flex;
  position: relative;
}

.big__section__icon, .small__section__icon {
  background-image: url("Search.ccd3c07b.svg");
  margin: auto 0;
  position: absolute;
}

.big__section__icon {
  width: 19px;
  height: 19px;
  left: 25px;
}

.small__section__icon {
  object-fit: cover;
  background-size: cover;
  width: 11px;
  height: 11px;
  left: 13px;
}

input {
  color: #3d4e61;
  border: 1px solid #e1e7ed;
  border-radius: 4px;
  width: 100%;
  padding: 0;
  font-family: Avenir, sans-serif;
  font-weight: 300;
  box-shadow: 0 1px 8px #3d4e611a;
}

.big__section__input {
  padding-left: 62px;
  font-size: 16px;
  line-height: 68px;
}

.small__section__input {
  padding-left: 33px;
  font-size: 14px;
  line-height: 40px;
}

input:hover {
  box-shadow: 0 3px 8px #3d4e6133;
}

input:focus {
  text-shadow: 1px 4px 4px #00000040;
  background: linear-gradient(#fff 0%, #f6f6f7 100%);
  border-radius: 3px;
  font-weight: 700;
  box-shadow: 0 4px 4px #00000040;
}

